import axios from "axios";
import {useNavigate} from "react-router-dom";
import React, {useEffect, useState} from "react";
import {CCard, CCardHeader, CCardBody} from "@coreui/react";
import TableUserGroups from "./TableUserGroups";
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../../utils/Common";
import Modal from "react-bootstrap/Modal";
import GroupForm from "./GroupForm";
import GroupFormEdit from "./GroupFormEdit";

const ManageAgentsGroup = () => {
  const [groups, setGroups] = useState(null)

  async function FetchUserGroups() {
    try {
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/user-group2";

      let res = await axios.get(url, {
        headers: {'Authorization': `Bearer ${token}`}
      });
      // console.log(res);
      // console.log(res.data.error);
      // console.log(res.data.recordset);

      if (res.status === 200 && !res.data.error) {
        setGroups(res.data.recordset);
      }
    } catch (e) {
      errorToast(e.message);
    }
  }

  useEffect(() => {
    FetchUserGroups().then();
  }, []);

  /* ------------------------------------- */

  const deleteHandler = async (GroupID) => {
    if (confirm("Are you sure?")) {
      try {
        let token = getCookieValue(tokenName);
        let url = ipAddress + "admin/user-group2/" + GroupID;

        let res = await axios.delete(url, {
          headers: {'Authorization': `Bearer ${token}`}
        });

        if (res.status === 200 && res.data.error) {
          errorToast(res.data.message);
        } else if (res.status === 200 && !res.data.error) {
          FetchUserGroups().then();
          successToast(res.data.message);
        }
      } catch (e) {
        errorToast(e.message);
      }
    }
  };

  /* ------------------------------------- */

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  /* ------------------------------------- */
  const [showEdit, setShowEdit] = useState(false);
  const handleCloseEdit = () => setShowEdit(false);
  const handleShowEdit = () => setShowEdit(true);

  const [userGroup, setUserGroup] = useState(null);

  const showDataInModal = (row) => {
    setUserGroup(row);
    handleShowEdit();
  }


  return (
    <>
      <CCard>
        <CCardHeader className="card-header-bg">
          <div className="row">
            <div className="col-8">
              <h4 className="mb-0 primary-color">User Group</h4>
            </div>

            <div className="col-4 text-end">
              <button onClick={handleShow} className="btn btn-primary btn-sm">
                Add User Group <i className="fa fa-plus"></i>
              </button>
            </div>
          </div>
        </CCardHeader>

        <CCardBody>
          {groups ? groups.length > 0 &&
            <TableUserGroups data={groups} deleteHandler={deleteHandler} showDataInModal={showDataInModal}/> :
            <div className="text-center">
              <i className="spinner spinner-border"></i>
            </div>
          }
        </CCardBody>
      </CCard>

      {/* Modal - Create User Group */}
      <Modal show={show} onHide={handleClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Create User Group</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <GroupForm FetchUserGroups={FetchUserGroups}/>
        </Modal.Body>
      </Modal>

      {/* Modal - Edit/Update User Group */}
      <Modal show={showEdit} onHide={handleCloseEdit} centered>
        <Modal.Header closeButton>
          <Modal.Title>Edit User Group</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <GroupFormEdit userGroup={userGroup} FetchUserGroups={FetchUserGroups}/>
        </Modal.Body>
      </Modal>
    </>
  )
}
export default ManageAgentsGroup;
