import axios from "axios";
import {useForm} from "react-hook-form";
import Button from "react-bootstrap/Button";
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../../utils/Common";
import React from "react";

const GroupForm = ({userGroup, FetchUserGroups}) => {
  const {
    register,
    handleSubmit,
    formState: {errors}
  } = useForm({defaultValues: userGroup});

  async function onSubmit(data) {
    try {
      const token = getCookieValue(tokenName);
      const res = await axios.put(ipAddress + "admin/user-group2/" + data.GroupID, data, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        }
      })
      // console.log(res)
      // console.log(res.data)

      if (res.status === 200 && res.data.error) {
        errorToast(res.data.message)
      } else if (res.status === 200 && !res.data.error) {
        FetchUserGroups();
        successToast(res.data.message)
      }
    } catch
      (e) {
      // console.log(e)
      errorToast(e.message)
    }
  }

  return (
    <>
      <form onSubmit={handleSubmit(onSubmit)}>
        {/* Group Name */}
        <div className="mb-3">
          <label htmlFor="GroupName">Group Name <span className="text-danger">*</span></label>
          <input type="text" id="GroupName" name="GroupName" className="form-control" readOnly
                 {...register('GroupName', {
                   required: "This is a required field.",
                   validate: value => !/<|>/.test(value) || 'Name cannot contain "<" or ">"'
                 })}
          />

          {errors?.GroupName && <p className='text-danger'>{errors?.GroupName?.message}</p>}
        </div>

        {/* Group Name */}
        <div className="mb-3">
          <label htmlFor="Description">Description <span className="text-danger">*</span></label>
          <input type="text" id="Description" name="Description" className="form-control"
                 {...register('Description', {
                   required: "This is a required field.",
                   validate: value => !/<|>/.test(value) || 'Name cannot contain "<" or ">"'
                 })}
          />

          {errors?.Description && <p className='text-danger'>{errors?.Description?.message}</p>}
        </div>

        <button className="btn btn-primary px-5">Create</button>
      </form>
    </>
  )
}
export default GroupForm;
