import React, {useMemo} from "react";
import DataTable from 'react-data-table-component';
import FilterComponent from "../agents/FilterComponent";
import {customStyles} from "../../../utils/Common"

const TableAgents = (props) => {
  const columns = [
    {
      name: "GroupName",
      selector: row => row.GroupName,
      sortable: true,
    },
    {
      name: "Description",
      selector: row => row.Description,
      sortable: true,
    },
    {
      name: "CreatedDate",
      selector: row => row.CreatedDate,
      sortable: true,
      width: '160px'
    },
    {
      name: "CreatedBy",
      selector: row => row.CreatedBy,
      sortable: true
    },
    {
      name: "UpdatedDate",
      selector: row => row.UpdatedDate,
      sortable: true,
      width: '160px'
    },
    {
      name: "UpdatedBy",
      selector: row => row.UpdatedBy,
      sortable: true,
      width: '180px'
    },
    {
      button: true,
      cell: row => <>
        <button onClick={() => props.showDataInModal(row)} className="btn btn-link">
          Edit
        </button>

        <button onClick={() => props.deleteHandler(row.GroupID)} className="btn btn-link">
          Delete
        </button>
      </>
    },
  ];

  const [filterText, setFilterText] = React.useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = React.useState(false);

  const filteredItems = props.data.filter(
    item => JSON.stringify(item).toLowerCase().indexOf(filterText.toLowerCase()) !== -1
  );

  const subHeaderComponent = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <>
        <FilterComponent
          onFilter={e => setFilterText(e.target.value)}
          onClear={handleClear}
          filterText={filterText}
        />
      </>
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <>
      <DataTable
        columns={columns}
        data={filteredItems}
        striped
        pagination
        subHeader
        subHeaderComponent={subHeaderComponent}
        customStyles={customStyles}
      />
    </>
  )
}
export default TableAgents;
